import React from "react";
import './contact.css';
import Card3 from "src/components/card3/Card3";
import {BsLinkedin, BsGithub} from "react-icons/bs";
import {MdOutlineEmail} from "react-icons/md";
import {RiMessengerLine} from "react-icons/ri";


const Contact = () => {
  
  return (
    <section id="contact" className="contact">

      <div className="section-header">
        <h5 className="color-primary-text-dark">Skontaktujmy się</h5>
        <h2 className="color-secondary">Kontakt</h2>
      </div>

      <div className="contact-cards">

        <Card3>
          <div className="contact-card">
            <MdOutlineEmail size={24} className='color-secondary'/>
            <h4 className="color-primary-text-dark">mateusz.chalik</h4>
            <a href="mailto:mateusz.chalik@gmail.com" className="button button-dark">Wyślij wiadomość</a>
          </div>
        </Card3>

        <Card3>
          <div className="contact-card">
            <RiMessengerLine size={24} className='color-secondary'/>
            <h4 className="color-primary-text-dark">Mateusz Chalik</h4>
            <a href="https://m.me/mateusz.chalik" className="button button-dark">Wyślij wiadomość</a>
          </div>
        </Card3>

        <Card3>
          <div className="contact-card">
            <BsLinkedin size={24} className='color-secondary'/>
            <h4 className="color-primary-text-dark">Mateusz Chalik</h4>
            <a href="https://www.linkedin.com/in/mateusz-chalik-559813233/" target='_blank' className="button button-dark">Zobacz profil</a>
          </div>
        </Card3>

        <Card3>
          <div className="contact-card">
            <BsGithub size={24} className='color-secondary'/>
            <h4 className="color-primary-text-dark">matCh00</h4>
            <a href="https://github.com/matCh00" target='_blank' className="button button-dark">Zobacz profil</a>
          </div>
        </Card3>

      </div>
      
    </section>
  )
}

export default Contact;