import React, {useRef} from "react";
import './card3.css';


type Props = {
  children: React.ReactNode;
  glowColor?: string;
  shineOpacity?: number;
  glow?: boolean;
};

const Card3 = ({children, glowColor = "var(--color-primary-text-dark)", shineOpacity = 0.1, glow = true}: Props) => {

  const ref = useRef<HTMLDivElement | null>(null);

  const handleMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
    const el = ref.current;
    if (!el) return;

    const rect = el.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;

    const percentX = x / rect.width;
    const percentY = y / rect.height;

    el.style.setProperty("--shine-x", `${percentX * 100}%`);
    el.style.setProperty("--shine-y", `${percentY * 100}%`);
  };

  return (
    <div
      className={`shine-card3 ${glow ? "glow" : ""}`} ref={ref}
      onMouseMove={handleMouseMove} style={{"--glow-color": glowColor, "--shine-opacity": shineOpacity} as React.CSSProperties}
    >
      
      <div className="shine-card3-inner">{children}</div>

    </div>
  );
}

export default Card3;
